.chart {
	width: 300px;
	margin-top: 35px;
  display: flex;
  flex-flow: column;
}

.chart--data-points {
  display: flex;
  flex: 1 1 auto;
  flex-flow: row wrap;
  justify-content: space-between;
}

.chart--title {
  flex: 0 1 auto;
  font-family: arial;
  font-size: 12px;
  font-weight: bold;
  margin-top: 3px;
  text-align: center;
  text-transform: uppercase;
  width: 100%;
}



