@import "bourbon/bourbon";
@import "svg-sprite-mixins";


// Usage for breakpoint mixin. ex.( @include respond-to($device){} )
@mixin respond-to($device) {
  @if $device == seriously {
    @media screen and (min-width: 1px) and (max-width: 319px) {
      @content;
    }
  }

  @else if $device == mlb {
    @media screen and (min-width: 1px) and (max-width: 767px), screen and (min-device-width : 1px) and (max-device-width : 767px) {
      @content;
    }
  }

  @else if $device == mobile {
    @media screen and (min-width: 320px) and (max-width: 767px), screen and (min-device-width : 320px) and (max-device-width : 767px) {
      @content;
    }
  }

  @else if $device == mobile-portrait {
    @media screen and (min-width: 320px)and (max-width:479px), screen and (min-device-width : 320px) and (max-device-width : 479px) and (orientation : portrait) {
      @content;
    }
  }

  @else if $device == mobile-landscape {
    @media screen and (min-width: 480px) and (max-width: 767px), screen and (min-device-width : 320px) and (max-device-width : 767px) and (orientation : landscape) {
      @content;
    }
  }

  @else if $device == tablet {
    @media screen and (min-width: 768px) and (max-width: 1023px), screen and (min-device-width : 768px) and (max-device-width : 1023px) {
      @content;
    }
  }

  @else if $device == broadband {
    @media screen and (min-width: 1024px) {
      @content;
    }
  }

  @else if $device == tablet-plus {
    @media screen and (min-width: 768px) {
      @content;
    }
  }

  // This breakpoint is used for mobile to tablet webview sizes. The tablet webview is currently set to 700px.
  @else if $device == mobile-app-webview {
     @media screen and (min-width: 320px) and (max-width: 699px), screen and (min-device-width : 320px) and (max-device-width : 699px) {
      @content;
    }
  }

  // This breakpoint is used for desktop GameCenterLive Small view (<1280 screen width)
  @else if $device == gcl-small {
    @media screen and (min-width: 1px) and (max-width: 1279px), screen and (min-device-width : 1px) and (max-device-width : 1279px) {
      @content;
    }
  }

  // This breakpoint is used for desktop GameCenterLive Large view (>1280 screen width)
  @else if $device == gcl-large {
     @media screen and (min-width: 1280px) {
      @content;
    }
  }

  // This breakpoint is used for desktop GameCenterLive Large view (>1280 screen width)
  @else if $device == gcl-large-neulion {
     @media screen and (min-width: 1292px) and (min-height: 900px) {
      @content;
    }
  }

  // This warning basically fires when an include is pointing to an undefined breakpoint.
  @else {
    @warn "respond-to breakpoint mixin only supports: 'seriously', 'mlb', 'mobile', 'mobile-portrait', 'mobile-landscape', 'tablet', 'broadband' and 'mobile-app-webview'";
  }
}

@mixin fontpair($size) {
  @if length($size) == 1 {
    @if unitless($size) {
      font-size: $size + px;
      line-height: $size + px;
    }

    @if not(unitless($size)) {
      font-size: $size;
      line-height: $size;
    }
  }

  @if length($size) == 2 {
    $fs: nth($size, 1);
    $lh: nth($size, 2);

    @if unitless($fs) {
      font-size: $fs + px;
    }

    @if not(unitless($fs)) {
      font-size: $fs;
    }

    @if unitless($lh) {
      line-height: $lh + px;
    }

    @if not(unitless($lh)) {
      line-height: $lh;
    }
  }
}

