// Support for mozilla in experimental css3 properties (-moz).
$experimental-support-for-mozilla      : true !default;
// Support for webkit in experimental css3 properties (-webkit).
$experimental-support-for-webkit       : true !default;
// Support for webkit's original (non-standard) gradient syntax.
$support-for-original-webkit-gradients : true !default;
// Support for opera in experimental css3 properties (-o).
$experimental-support-for-opera        : true !default;
// Support for microsoft in experimental css3 properties (-ms).
$experimental-support-for-microsoft    : true !default;
// Support for khtml in experimental css3 properties (-khtml).
$experimental-support-for-khtml        : false !default;
// Support for svg in experimental css3 properties.
// Setting this to true might add significant size to your
// generated stylesheets.
$experimental-support-for-svg          : false !default;
// Support for CSS PIE in experimental css3 properties (-pie).
$experimental-support-for-pie          : false !default;




// This mixin provides basic support for CSS3 properties and
// their corresponding experimental CSS2 properties when
// the implementations are identical except for the property
// prefix.
@mixin experimental($property, $value,
  $moz      : $experimental-support-for-mozilla,
  $webkit   : $experimental-support-for-webkit,
  $o        : $experimental-support-for-opera,
  $ms       : $experimental-support-for-microsoft,
  $khtml    : $experimental-support-for-khtml,
  $official : true
) {
  @if $webkit  and $experimental-support-for-webkit    { -webkit-#{$property} : $value; }
  @if $khtml   and $experimental-support-for-khtml     {  -khtml-#{$property} : $value; }
  @if $moz     and $experimental-support-for-mozilla   {    -moz-#{$property} : $value; }
  @if $ms      and $experimental-support-for-microsoft {     -ms-#{$property} : $value; }
  @if $o       and $experimental-support-for-opera     {      -o-#{$property} : $value; }
  @if $official                                        {         #{$property} : $value; }
}

// Round all corners by a specific amount, defaults to value of `$default-border-radius`.
//
// When two values are passed, the first is the horizontal radius
// and the second is the vertical radius.
//
// Note: webkit does not support shorthand syntax for several corners at once.
// So in the case where you pass several values only the first will be passed to webkit.
//
// Examples:
//
//     .simple   { @include border-radius(4px, 4px); }
//     .compound { @include border-radius(2px 5px, 3px 6px); }
//     .crazy    { @include border-radius(1px 3px 5px 7px, 2px 4px 6px 8px)}
//
// Which generates:
//
//    .simple {
//      -webkit-border-radius: 4px 4px;
//      -moz-border-radius: 4px / 4px;
//      -khtml-border-radius: 4px / 4px;
//      border-radius: 4px / 4px; }
//
//    .compound {
//      -webkit-border-radius: 2px 3px;
//      -moz-border-radius: 2px 5px / 3px 6px;
//      -khtml-border-radius: 2px 5px / 3px 6px;
//      border-radius: 2px 5px / 3px 6px; }
//
//    .crazy {
//      -webkit-border-radius: 1px 2px;
//      -moz-border-radius: 1px 3px 5px 7px / 2px 4px 6px 8px;
//      -khtml-border-radius: 1px 3px 5px 7px / 2px 4px 6px 8px;
//      border-radius: 1px 3px 5px 7px / 2px 4px 6px 8px; }

$default-border-radius: 5px !default;

@mixin border-radius($radius: $default-border-radius, $vertical-radius: false) {

  @if $vertical-radius {
    // Webkit doesn't understand the official shorthand syntax for specifying
    // a vertical radius unless so in case there's several we only take the first.
    @include experimental(border-radius, first-value-of($radius) first-value-of($vertical-radius),
      not -moz,
      -webkit,
      not -o,
      not -ms,
      not -khtml,
      not official
    );
    @include experimental("border-radius", $radius unquote("/") $vertical-radius,
      -moz,
      not -webkit,
      not -o,
      not -ms,
      -khtml,
      official
    );
  }
  @else {
    @include experimental(border-radius, $radius);
  }
}

@mixin site-gradient($top-color, $bottom-color, $fade:even, $direction:vertical, $orientation:top){
  @if $fade == split {
    @include linear-gradient(left, $top-color 0%,$top-color 50%,$bottom-color 50%,$bottom-color 100%);
  }
  @else if $fade == even {
    @include linear-gradient($orientation,  $top-color 0%, $bottom-color 100%);
  }
}

@mixin fullBackgroundImage($url, $size:cover, $bgcolor:transparent, $position:center center){
  background-color: $bgcolor;
  background-image: url($url);
  background-position: $position;
  background-size: $size;
  background-repeat: no-repeat;
}

@mixin center {
  max-width: $max-width;
  margin: 0 auto;
}

@mixin fullWidthSection {
  width: 100%;
  background-color: $site-primary-color;
}

@mixin broadcastLogo($height, $mr:10px){
  height:$height;
  margin-right:$mr;
}

@mixin broadcastLayout($height, $ta:right){
  height:$height;
  text-align:$ta;
}

@mixin broadcastSpan($height, $fs:14px){
  line-height:$height;
  font-size:$fs;
}

@mixin nativeFontFamily($fontname, $fontweight:normal, $fontstyle:normal){
  @font-face {
    font-family: $fontname;
    font-weight: $fontweight;
    font-style: $fontstyle;
  }
}

@mixin bgImageSize($w, $h){
  position: relative;
  @include size($w $h);
  background-size: $w $h;
  background-repeat: no-repeat;
}

@mixin vertical-align {
  position: relative;
  top: 50%;
  -webkit-transform: translateY(-50%);
  -ms-transform: translateY(-50%);
  transform: translateY(-50%);
}


// Awesome Rotating CSS Arrows
// 1.  To set the arrow pointing up, set border-width to ie. [border-width: 0 4px 5px 4px;]
//     Also set the border-color to ie. [border-color: transparent transparent #7D7D7D transparent;]
//
// 2.  To set the arrow pointing down, set border-width to ie. [border-width: 5px 4px 0 4px;]
//     Also set the border-color to ie. [border-color: transparent transparent #7D7D7D transparent;]
//
// 3.  To animate the arrow use [@include transform(rotate(180deg));] on the active class of the arrow.

@mixin triggerArrow($arrowDirection:"down", $includeTransition:false, $defaultColor:#FFFFFF, $activeColor:#FFFFFF){
  padding: 0;
  width: 0;
  height: 0;
  border-style: solid;
  /* 1. */
  border-width: 0 4px 5px 4px;
  @if $arrowDirection == "down"{
    @include transform(rotate(180deg));
  }
  border-color: transparent transparent $defaultColor transparent;

  @if($includeTransition){
    @include transition-property (transform);
    @include transition-duration(0.25s);
    @include transition-timing-function(ease);
    @include transition-delay(0s);
  }

  &.active {
    @if($includeTransition){
      /* 3. */
      @include transform(rotate(180deg));
    }
    border-color: transparent transparent $activeColor transparent;
  }
}




