@import "../../../../_sass/sass-includes";

div#statsPage {
	display: flex;
	flex-direction: column;
	background-color: $_EC;
}

.index .stats-container {
	display: flex;
	flex-direction: column;
	margin-bottom: 20px;
	padding: 2%;
	background-color: $_F;
	box-shadow: 0 2px 3px rgba(0, 0, 0, 0.1);

	h3 {
		white-space: nowrap;
		text-transform: uppercase;
	}

  header {
    font-size: 22px;
  }
}

.index .split-stats-container header {
  font-size: 22px;
}

.index .split-stats-container {
	display: flex;
	justify-content: space-between;
	flex-wrap: wrap;

	& .split-stats {
    width: 100%;
    padding: 2%;
		margin-bottom: 20px;
		background-color: $_F;
		box-shadow: 0 2px 3px rgba($_0,.1);

		h3 {
			white-space: nowrap;
			text-transform: uppercase;
		}

		& .stats-category {
			width: 50%;
		}
	}
}

.index .stats-group {
	display: flex;
  flex-flow: row wrap;
	justify-content: space-around;
}

.index .stats-group .stat-details .category-name {
  margin-top: 0;
}

.index .stats-group .trending-list {
  display: none;
}

.index .stats-category.team-leaderboard {
  margin: 30px 0 0 0;
  width: 50%;

  .stats-leader {
    margin: 0;
    width: auto;
    width: initial;

    .team-details {
      font-size: 16px;
    }
  }
}

.index .split-stats-container .stats-category.team-leaderboard {
  width: 33%;
}

.content > .loading-overlay {
  background-color: #fff;
  display: block;
  padding: 10px;
  text-align: center;
}


#nhl-live-container div {
  position: relative;
  margin-top: 10px;
}

.statsNarrowMod img.ratio {
  display: block;
  width: 100%;
  height: auto;
}

.statsNarrowMod iframe {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}



/*===== Media queries =====*/
@media only screen and (min-width: 1024px) {
  .index .stats-container {
    padding: 25px;
  }

  .index .stats-container header,
  .index .split-stats-container header {
    font-size: 29px;
  }

  .index .stats-category.team-leaderboard,
  .index .stats-group .stats-leader {
    width: auto;
    width: initial;
  }

  .index .stats-group .trending-list {
    display: block;
  }

  .index .split-stats-container .split-stats {
		padding: 20px;
		width: calc(50% - 10px);
  }
}



