@import "../../../../_sass/sass-includes";

.stats-leader {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 30px;
  width: 50%;

  h3 {
    font-size: 20px;
    margin: 10px 0;
  }

  .stat-details {
    align-items: center;
    display: flex;
    flex-direction: column;
    font-weight: 700;
    margin-top: 15px;
    text-align: center;

    p {
      @include fontpair(12px);
      font-family: Arial,sans-serif;
      margin: 15px 0 0 0;

      &.category-name {
        text-transform: uppercase;
      }

      &.large-stat {
        @include fontpair(44px);
        font-family: Oswald-Regular,sans-serif;
        margin: 8px 0 0 0;

        span {
          font-family: Sintony-Bold, sans-serif;
          font-size: 30px;
          line-height: 30px;
          vertical-align: super;
        }
      }

      &.team-details {
        @include fontpair(18px);
        font-family: Sintony-Regular,sans-serif;
        text-transform: none;
        color: $_3;
      }
    }
  }
}

.silo-player-img-container {
  position: relative;
  
  .team-logo {
    bottom: 0;
    position: absolute;
    right: -20px;
  }
}

/*===== Media queries =====*/
@media only screen and (min-width: 1024px) {
  .stats-leader {
    width: auto;
    width: initial;

    h3 {
      margin: 0 0 25px 0;
    }
  }
}



