/*! print.css */

@import "../../../../../../../../_sass/sass-includes";

@media print {

    /******** hide these items from being printed ******/
    #masthead,
    #langSignIn,
    #leagueSiteMenu,
    #leaderBoardAd,
    #statsNavi,
    #c_copyright,
    #quicklinks,
    #enhanced-container,
    #reportSelectorContainer,
    #stats-data-table_info,
    #stats-data-table_paginate,

    .toolbar-links,
    .filter-container,
    .paging,
    .trending-list,
    .advanced-tabs,
    .FixedHeader_Cloned,
    .table-selection-checkbox,

    #statsPage.index,
    #statsPage.leaders



    #leaderBoardAd,
    #masthead,
    #promoBanner,
    #leagueSiteMenu,
    #c_drippan,
    #pageHeader,
    #standingsTabs,
    #standingsLegend,
    #medCol,
    #copyright,
    #seasonSelection,
    #c_standingsAffiliate,
    #schedForm,
    .newsTools,
    .blogTools,
    #dateNavi,
    #topNav,
    #relatedNews,
    #inbodyRelMed,
    #inbodyRelMed .links,
    .ui-tabs-nav,
    #divButtons,
    #commentPrintButtons,
    #pageWrapper.schedule #wideCol,
    #pageWrapper.schedule .sectionHeader.wide,
    .sectionHeader,
    #pageWrapper.schedule .skedLinks,
    #header,
    #gcGmSbWrap,
    #gcTools,
    table.statsHeader th,
    table.statsHeader td,
    .leaderBlock,
    .statsFormTable,
    .pageNaviBar,
    .tableLegendBtn,
    .gameVideo,
    .gameVideoMore,
    .numRes,
    .pages,
    .inlineThreeStars,
    .inbody-media,
    .playerCard,
    #gcTopAds,
    #playerSite,
    #seasonForm,
    .btn,
    .modContainer,
    #dripTopAd,
    #nonNhlLogo,
    .langs,
    #rfanLoginBubble,
    #signInRegArea,
    #commentsDiv,
    #quicklinks,
    #evp,
    #GameCenterModule7,
    #ipVidCont,
    #playoffNavi,
    .inlineStats,
    .socialShareBar,
    #privacyPolicy,
    .megamenu,

    // Charts
    g.highcharts-navigator
    {
      display: none !important;
    }

    /********** shown items tweaked for printing ********/

    body {
        background-image: none;
        background-color: #fff;
        font: 11px verdana
    }

    a:link, a:visited {
        color: #000;
        text-decoration:none;
    }

    .toolbar-breadcrumbs {
        padding-bottom: 10pt;
    }

    table {
        border-top-width: 0;
        border-right-width: 0;
        border-bottom-width: 0;
        border-left-width: 0;
        border-spacing: 0;
    }

    #tableContainer {
        clear: left;
    }

    th, td {
        border-right: 1px solid #000;
        padding: 2px;
        border-bottom: 1px solid #000;
    }

    #statsHeader h1 {
        float: left;
    }

    #statsPage .navi-sap-header-ad {
        float: right;
    }

    #statsPage .toolbar {
        clear: left;
    }

    // Charts
    .charts-container {

        &__tile-container {
            width: 100%;
            padding: 20px;
            float: left;

            .statslab-tile-right-side {
                float: right;

                span.statslab-stat-aggregate {
                    display: block;
                    font-size: 24px;
                    padding-right: 5px;
                    text-align: right;
                    color: darken($_7, 20%);
                }

            }

            .statslab-tile {
                position: relative;
                display: block;
                float: left;
                background-color: $_E;
                color: $_0;
                padding: 10px;
                width: calc(19%);
                font-family: Arial, sans-serif;
                margin: 0 5px;
                border-style: solid;
                border-width: 0;
                border-left-width: 12px;

                &:nth-child(1) {
                    margin-left: 0;
                    //width: calc(25% - 5px);
                }

                &:nth-child(4) {
                    margin-right: 0;
                    //width: calc(25% - 5px);
                }

                &__headshot {
                    display: inline-block;
                    overflow: hidden;
                    width: 110px;
                    height: 84px;
                    margin: 0 0 -13px -10px;
                    padding: 0;
                }

                &__detail {
                    display: inline-block;
                    width: calc(100% - 100px);
                    vertical-align: top;
                }

                &__name {
                    display: block;
                    font-size: 16px;
                    line-height: 16px;
                    padding-top: 2px;
                    padding-bottom: 4px;
                    color: darken($_7, 20%);

                    span {
                        display: block;
                    }
                }

                &__description {
                    display: block;
                    font-size: 12px;
                    color: darken($_D3, 20%);
                }

                &__close {
                    display: none;

                    span {
                        width: 30px;
                        height: 30px;
                        font-size: 24px;
                        display: inline-block;
                        text-align: center;
                        color: $blue18;

                        &:hover {
                            cursor: pointer;
                        }
                    }
                }

                &__team {
                    position: absolute;
                    bottom: 5px;
                    right: 5px;
                    width: 48px;
                    height: 32px;

                    img {
                        width: 48px;
                        height: 32px;
                    }
                }

                // Team tiles
                &__team.team {
                    display: inline-block;
                    width: 72px;
                    height: 48px;
                    margin: 0 10px 0 0;
                    padding: 0;
                    position: relative;
                    right: 0;
                    bottom: 0;

                    img {
                        width: 72px;
                        height: 48px;
                    }
                }
            }
        }

        & #PlayerComparisonChart {
            clear: both;
        }
    }

    .stats-toolbar {
        display: none!important;
    }

    .navi-sap-header-ad {
        display: none!important;
    }

}// end @media print







