.horizontal-bar-chart-container {
  display: flex;
  margin: 20px 5% 0;
  overflow: hidden;
  padding: 0 10px;
}

.horizontal-bar-chart {
  display: flex;
  flex: 1 1 auto;
  flex-flow: row wrap;
}

.horizontal-bar-chart--padding {
  flex: 0 1 15%;
}

.horizontal-bar-chart--data-point {
  display: flex;
  height: 40px;
  width: 100%;
}

.horizontal-bar-chart--data-point--value {
  animation: fade-in .5s ease-out 1s forwards;
  font-family: Oswald-Regular;
  font-size: 18px;
  left: 100%;
  margin-left: 5px;
  opacity: 0;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}

.horizontal-bar-chart--x-axis {
  display: flex;
  justify-content: space-between;
  position: relative;
  width: 100%;

  .horizontal-bar-chart--x-axis--tick {
    font-family: arial;
    font-size: 12px;
    margin-top: 15px;
    position: relative;

    &:before {
      color: #ccd6eb;
      content: "|";
      font-size: 7px;
      left: 50%;
      position: absolute;
      top: -9px;
      transform: translateX(-50%);
    }

    &:first-child:before {
      left: 0;
    }

    &:last-child {
      font-weight: bold;
    }
  }

  // The below is to make the CSS chart match HighChart's style
  .horizontal-bar-chart--x-axis--tick:last-child {
    margin-right: -10px;
    position: relative;
  }
}


@keyframes slide-ltr {
  from {
    transform: translateX(-100%);
  }

  to {
    transform: translateX(0);
  }
}

@keyframes fade-in {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}



