@media print {
  a {
    text-decoration: none;
  }

  .rt-thead.-filters {
    display: none;
  }

  // There are importants here to override the inline styles created by React Table
  // The styles enable the columns to be closer together so none are cut out
  .rt-thead.-header,
  .rt-tbody {
    min-width: unset !important;
  }

  .rt-resizable-header,
  .rt-td {
    align-items: center;
    display: flex;
    flex: auto !important;
    justify-content: center;
    padding: 0 !important;
    width: 30px !important;
  }

  .rt-th.-sort-asc::after,
  .rt-th.-sort-desc::after {
    top: 100% !important;
  }

  .rt-th.primary-sort::after,
  .rt-th.secondary-sort::after,
  .rt-th.tertiary-sort::after {
    print-color-adjust: exact;
    -webkit-print-color-adjust: exact;
  }
}



