@import '../../../../../../../../../../../../../../_sass/sass-includes';

.column-filter {
  display: flex;
  padding: 5px 0;

	& > div, > select, > input {
		margin-right: 5px;
	}

	& select {
		max-width: 105px;
		width: auto;
	}

	& > input {
		width: 84px;
		height: 22px;
		border: 1px solid $_BE;
		border-radius: 4px;
		background-color: $_F;
		box-shadow: none;
		padding: 0 8px;
	}

	&__btn {
		color: $blue18;
		cursor: pointer;
		height: 22px;
		width: 22px;
		line-height: 18px;
		text-align: center;

		&--add {
			background-color: $_F;
			@include background-image(linear-gradient(top, $_F 0%, $_F1 100%));
			border: 1px solid $_BE;
			border-radius: 4px;
			font-size: 18px;
		}

		&--remove {
			color: $blue18;
			font-size: 24px;
			line-height: 20px;
		}
	}

}



