.warning {
  background-color : rgba(255,255,0,.25);
}

.message-center-container {
  width: 100%;
  text-align: center;
  float: left;
  cursor: pointer;
  padding-top: 5px;
  padding-bottom: 5px;

  -webkit-transition: height .25s;
  transition: height .25s;

  &.hidden {
    height: 0px;
    padding-top: 0px;
    padding-bottom: 0px;
  }

  .warning-close-button {
    padding-left : 5px;
  }
}

.toolbar-button.disabled {
  background: none #DADADA;
  border: 1px solid #DADADA;
  color: #FFFFFF;
  cursor: not-allowed;
}



