/* NOTE:
 * Original styling can be found:
 * https://github.com/Rudeg/react-input-calendar/blob/master/style/index.css
 */

.input-calendar {
  font-family: 'Sintony', Arial, sans-serif;
  font-weight: 700;
  position: relative;
  z-index: 2;
}

.input-calendar-wrapper {
  position: absolute;
  box-shadow: 0 0 9px 5px rgba(0,0,0,0.05);
  background-color: #fff;
  text-align: center;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  border: 1px solid rgba(0,0,0,.15);
  left: 0;
  padding: 8px;
}

.input-calendar-wrapper .days-view {
  width: 288px;
}

.input-calendar-wrapper .months-view,
.input-calendar-wrapper .years-view {
  width: 217px;
}

.input-calendar-wrapper .months-view .months,
.input-calendar-wrapper .years-view .years {
  padding: 16px 12px;
}

.input-calendar .input-calendar-field {
  width: 100%;
  border: 1px solid #ccc;
  border-radius: 4px;
  padding: 2px 8px;
  font-size: 11px;

  &:disabled {
    color: #d6d6d6;
  }

  &:focus {
    outline: none;
    border: 1px solid #0183da;
  }
}

.input-calendar .calendar-icon {
  display: inline-block;
  padding: 0px 1px 0 5px;
  height: 27px;
  line-height: 27px;
  transform: translateY(4px);
  cursor: pointer;
}

.input-calendar .days,
.input-calendar .months,
.input-calendar .years {
  padding-bottom: 2px;
}

.input-calendar .cell {
  display: inline-block;
  text-align: center;
  cursor: pointer;
  border: 1px solid #fff;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;

  &:hover {
    border: 1px solid #d2d2d2;
  }
}

.input-calendar .day {
  font-size: 14px;
  padding: 16px 0;
  width: 41px;
  height: 53px;
}

.input-calendar .disabled,
.input-calendar .day.prev,
.input-calendar .day.next {
  color: #ccc;
}

.input-calendar .month,
.input-calendar .year {
  border: none;
  font-size: 14px;
  width: 44px;
  height: 54px;
  margin: 2px;
  line-height: 54px;
  font-size: 14px;

  &:hover {
    background: #eee;
    border: none;
  }
}

.input-calendar .days-title .cell {
  cursor: default;
  color: #999;
  font-size: 12px;
  opacity: 1;
  padding: 20px 8px;

  &:hover {
    border: 1px solid #fff;
  }
}

.input-calendar .navigation-wrapper {
  border-bottom: 1px solid #d2d2d2;
}

.input-calendar .navigation-title {
  background: #0183da;
  display: inline-block;
  color: #fff;
  cursor: pointer;
  margin: 10px 0;
  padding: 10px;
  font-size: 16px;
}

.input-calendar .years-view .navigation-title {
  background: none;
  color: #000;
  cursor: default;

  &:hover {
    background: none;
    box-shadow:none;
    color: #000;
  }
}

.input-calendar .icon-wrapper {
  cursor: pointer;
}

.input-calendar .icon {
  cursor: pointer;
  font-size: 14px;
  padding: 20px 12px;
  width: 20px;

  &.disabled {
    cursor: default;
    visibility: hidden;
  }

  &:first-of-type {
    float: left;
  }

  &:last-of-type {
    float: right;
  }
}

.input-calendar .icon:hover,
.input-calendar .icon-wrapper:hover {
  color: #999;
}

.input-calendar .today-btn {
  background: #0183da;
  border-top: 1px solid #d2d2d2;
  color: #fff;
  cursor: pointer;
  display: block;
  font-size: 16px;
  margin-top: 12px;
  padding: 20px 12px;
  width: 100%;
}

.input-calendar .cell.day.today {
  border: 1px solid #0183da;
}

.input-calendar .navigation-title:hover,
.input-calendar .today-btn:hover {
  box-shadow: 0px 0px 5px rgba(48,48,48,0.75);
}



