/* General Layout */
.data-point--container {
  display: flex;
  flex-flow: column;
  text-align: center;
}

/* We want the bar to take up the majority of the space so we're taking advantage of flexbox (specifically flex-grow) */
.data-point--value,
.data-point--game-decision,
.data-point--description,
.data-point--game-date {
  flex: 0 1 auto;
}

.data-point--bar-container {
  align-items: center;
  display: flex;
  flex: 1 1 auto;
  flex-flow: column;
  height: 100%;
  justify-content: flex-end;

  // Set an overflow hidden so we can animate the bars in
  overflow: hidden;
}

.data-point--bar {
  margin-top: 2px;
}

.data-point--description {
  font-family: arial;
  font-size: 9px;
  font-weight: bold;
  margin-top: 1px;
  text-align: center;
  white-space: nowrap;
}

.data-point--description a {
  font-size: 8px;
  margin-top: 3px;
}

@keyframes slide-in {
  from {
    transform: translateY(100%);
  }

  to {
    transform: translateY(0%);
  }
}

@keyframes fade-in {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}



