// Print
@import 'print.pageWrapper';
@import 'print.statsTable';


/*===== General =====*/
.stats-table-view {
  justify-content: center;
  float: left;
  overflow: auto;
  width: 100%;
}

.ReactTable {
  font-family: Sintony, Arial;

  [title="Player Name"],
  [title="Season"] {
    text-align: left;
  }
}

.ReactTable .rt-table {
  overflow-x: auto;
  overflow-y: hidden;
  position: relative;
  z-index: 1;
  background: #fff;
}

.ReactTable.-striped .rt-tr {
  box-sizing: border-box;

  &.-odd {
    background: rgba(0, 0, 0, .1);
  }
}

// Reset the height to undo BAM styling
.ReactTable select {
  height: auto;
}

.ReactTable .rt-td {
  text-overflow: initial;
  white-space: normal;
}


/*===== Shadows to indicate to scroll =====*/
.ReactTable .rt-tbody .rt-tr-group {
  border-bottom: none;
}

.ReactTable .rt-tbody {
  position: relative;
}

.ReactTable.-highlight .rt-table .rt-tbody .rt-tr:not(.-padRow):hover {
  &.-even {
    background: #fff;
  }

  &.-odd {
    background: #ececec;
  }
}

/* NOTE
 * Whenever the datatable requires a horizontal scroll, shadows appear on the scrollable sides. This is accomplished
 * through using linear gradients.
 * The idea is we're applying a gradient in an oreo (yes, the cookie) fashion. There's a sliver of gray on both the 
 * beginning and end of the table, but they both fade into the white part right in the middle. Delicious! *badum tsst*
 */
.ReactTable .rt-table {
  background-image: linear-gradient(to right, #666, #fff 3%, #fff 97%, #666);
  background-size: 100% 100%;
}

.ReactTable .rt-tbody:before,
.ReactTable .rt-tbody:after {
  background: linear-gradient(to right, #fff, #fff 5%, rgba(255, 255, 255, 0));
  bottom: 0;
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  width: 10%;
  z-index: -1;
}

.ReactTable .rt-tbody:after {
  background: linear-gradient(to left, #fff, #fff 5%, rgba(255, 255, 255, 0));
  left: auto; // This is for IE 11
  left: initial;
  right: 0;
}


/*===== Loading =====*/
// Overwrite react-table's current loading styles
.-loading {
  // background: none;

  > div {
    color: initial;
  }
}

// Set an active class to mimic looking like a table row
.-loading .rt-noData {
  display: none;
}

.rt-noData {
  background: #fff;
  display: block;
  font-size: 12px;
  padding: 10px;
  text-align: center;
}

// .loading-overlay {
//   display: none;
// }

.-loading {
  .rt-tbody,
  .-pagination {
    display: none;
  }

  .loading-overlay {
    background: #fff;
    display: block;
    text-align: center;
  }
}


/*===== Filters =====*/
.-filters {
  height: 20px;
}

.-filters .rt-th {
  text-align: left;
}

.-filters input {
  line-height: 14px;
  padding: 0 4px 2px;
  width: 80%;
}

/*===== Column Headers =====*/
.ReactTable .rt-thead {
  background-color: #000;
  color: #fff;
  font-size: 12px;
}

.ReactTable .rt-thead .rt-th {
  overflow: visible; // Need this so the coloured columns show
  padding: 0 2px;
  white-space: normal;
}

.ReactTable .rt-thead {
  overflow: visible;
}

.ReactTable .rt-thead .rt-th .rt-header-cell,
.ReactTable .rt-thead .rt-th .rt-header-cell__left {
  align-items: center;
  display: flex;
  justify-content: center;
  line-height: 15px;
  padding: 10px 0;
  word-break: break-word;
}

.ReactTable .rt-thead .rt-th .rt-header-cell__left {
  justify-content: flex-start;
}

// Disable the index/rank column from showing a highlighted sorted style
.rt-thead .rt-tr .rt-th:first-child {
  background-color: #000 !important;
}

.ReactTable .rt-thead .rt-th.-sort-asc,
.ReactTable .rt-thead .rt-th.-sort-desc {
  background-color: #666;
  box-shadow: none;
}


/*===== Sorting =====*/
// Arrows
.rt-th.-sort-asc:not(.index-column) .rt-header-cell:after {
  content: '↑';
}

.rt-th.-sort-desc .rt-header-cell:after {
  content: '↓';
}

// Column highlighting -- essentially lays a long pseudo element over the column
.rt-th.-sort-asc::after,
.rt-th.-sort-desc::after {
  content: '';
  cursor: initial;
  height: 10000px;
  left: 0;
  pointer-events: none;
  position: absolute;
  top: calc(100% + 20px);
  width: 100%;
  z-index: 1;
}

.rt-th.primary-sort::after {
  background: rgba(1,131,218,.3);
}

.rt-th.secondary-sort::after {
  background: rgba(1,131,218,.2);
}

.rt-th.tertiary-sort::after {
  background: rgba(1,131,218,.1);
}


/*===== Sorting Tooltip =====*/
.ReactTable .rt-thead .rt-th.-cursor-pointer.index-column {
  cursor: help;
}

.ReactTable .rt-thead .index-column:active {
  pointer-events: none;
}

// The below enables the tooltip to appear underneath the column and styles the (i) icon
.ReactTable .rt-thead .index-column > div {
  overflow: visible;

  .rt-header-cell {
    position: relative;

    .rt-info-icon {
      border: 1px solid #fff;
      border-radius: 100%;
      padding: 1px 7px;
    }
  }
}

.ReactTable .rt-thead .index-column:after {
  content: 'Hold shift while selecting a column to sort by multiple columns.';

  background-color: #ececec;
  border: 1px solid #000;
  border-radius: 4px;
  color: #000;
  left: 5px;
  opacity: 0;
  padding: 4px;
  position: absolute;
  pointer-events: none;
  top: calc(100% + 30px);
  transition: .5s;
  width: 190px;
  white-space: normal;
  z-index: 1;
}

.ReactTable .rt-thead .rt-tooltip {
  background-color: #ececec;
  border: 1px solid #000;
  border-radius: 4px;
  color: #000;
  left: 5px;
  opacity: 0;
  padding: 4px;
  position: absolute;
  pointer-events: none;
  top: calc(100% + 30px);
  transition: .5s;
  width: 190px;
  white-space: normal;
  z-index: 1;

  .rt-tooltip-content {
    position: relative;

    &:before {
      border-bottom: 8px solid #000;
      border-left: 8px solid transparent;
      border-right: 8px solid transparent;
      content: '';
      height: 0;
      left: 2px;
      position: absolute;
      top: -13px;
      width: 0;
    }
  }
}


/*===== Pagination =====*/
.-pagination .-next .-btn,
.-pagination .-previous .-btn {
  color: #fff;
  border: 1px solid #bebebe;
  background-color: #0183da;
  font-size: 24px;
}

.ReactTable .-pagination .-btn:not([disabled]):hover {
  background-color: #0183da;
}

/*===== Interaction styles =====*/
.ReactTable.-highlight .rt-tbody .rt-tr:not(.-padRow):hover {
  outline: 1px solid rgba(1,131,218,1);

  &.-even {
    background: #ececec;
  }

  &.-odd {
    background: #fff;
  }


}

.ReactTable a:hover {
  cursor: pointer;
  font-weight: bold;
}

// Opacity is changed because transition does not work with display: none
.ReactTable .rt-thead .index-column:hover:after {
  opacity: 1;
}

/*===== Loading Component =====*/
//td element that actually holds everything
.stats-table-view.loading .stat-table tbody:nth-of-type(2) td {
  width: auto;
  display: block;
  background-color: white;
  text-align: center;
}



