//LEGEND
.stats-legend {
  position: relative;

  display: inline-block;

  width: 990px;
  padding: 10px;

  text-align: left;

  border-top-width: 1px;
  border-top-style: solid;
  border-top-color: #bebebe;
  background-color: #111;
}

.stats-legend-container {
  position: fixed;
  right: 0;
  bottom: -100%;
  left: 0;
  transition: .35s cubic-bezier(0.65, 0.05, 0.36, 1);
  text-align: center;
  z-index: 2;

  ul {
    display: none;
  }
}

.stats-legend-container.legend-active {
  bottom: 0;
  ul {
    display: block;
  }
}

.stats-legend__label {
  font-family: 'Sintony-Bold', sans-serif;
  font-size: 16px;
  line-height: 24px;

  margin-bottom: 10px;

  text-transform: uppercase;

  color: #fff;
  border-bottom: 1px solid #bebebe;
}

.stats-legend__item {
  font-family: 'Arial', sans-serif;
  font-size: 12px;
  line-height: 20px;

  display: inline-block;

  width: 33%;

  color: #fff;

  -webkit-font-smoothing: antialiased;
}

.stats-legend__item a {
  color: #fff;
}

.stats-legend__item span:first-of-type {
  font-weight: bold;
}

.stats-legend__close {
  font-size: 24px;

  position: absolute;
  top: 4px;
  right: 0;

  display: inline-block;

  width: 30px;
  height: 30px;

  text-align: center;

  color: #0183da;
}

.stats-legend__close:hover {
  cursor: pointer;
}




