.leaders-toolbar {
  background-color: white;
  border-bottom: 1px solid #d2d2d2;
  padding-bottom: 10px;
  padding-left: 25px;
  padding-top: 10px;
  width: 100%;
}

.leaders-toolbar .stats-option {
  margin-bottom: 10px;
}

.leaders-toolbar label {
  display: block;
  font-family: Sintony-Regular, sans-serif;
  font-size: 14px;
  font-weight: bold;
  margin-bottom: 5px;
  padding-right: 5px;
  text-transform: uppercase;
  vertical-align: middle;
}

.leaders-toolbar select {
  margin-right: 5px;
}

/*===== Media queries =====*/
@media only screen and (min-width: 1024px) {

  .leaders-toolbar .stats-option {
    display: inline-block;
    margin-bottom: 0;
  }

  .leaders-toolbar label {
    display: inline;
    margin-bottom: 0;
  }
}



