#drippan {
  ul {
    float: left;
    width: 100%;
    margin: 5px 0 0 5px;
    column-count: 5;
    column-gap: 10px;
    min-height: 420px;
    height: 420px;

    li {
      width: 100%;
      // -webkit-column-break-inside: avoid; /* Chrome, Safari, Opera */
      page-break-inside: avoid; /* Firefox */
      // break-inside: avoid; /* IE 10+ */

      a {
        min-width: 150px;
        display: block;
      }

      &:last-of-type {
        border-right: 0;
      }
    }
  }
}

// Media queries
// Tablet
@media screen and (max-width: 1023px) and (min-width: 768px), screen and (max-device-width: 1023px) and (min-device-width: 768px) {
  #drippan {
    width: 100%;

    & .secondaryNavi {
      width: 100%;
      margin: 0;

      .search {

        .box {
          width: calc(100% - 14px);
          height: 22px;
          line-height: 22px;
        }

        input[type="image"] {
          margin: 6px 0 6px 3px !important;
        }
      }
    }

    ul {
      column-count: 4;
      height: 475px;
    }
  }
}


// Mobile
@media screen and (max-width: 767px) and (min-width: 320px), screen and (max-device-width: 767px) and (min-device-width: 320px) {
  #leaderBoardAd {
    display: none;
  }

  //global imports
  #dripTopAd {
    width: 100%;
  }

  #drippan {
    width: 100%;
    margin: 0;
    padding: 5px;

    & .secondaryNavi {
      width: 100%;
      margin: 0;

      .search {
        width: 100%;

        .box {
          width: calc(100% - 14px);
          height: 22px;
          line-height: 22px;
        }

        input[type="image"] {
          margin: 6px 0 6px 3px !important;
        }
      }
    }

    & .links {
      font-family: Arial, sans-serif;
      line-height: 22px;
    }

    ul {
      width: 100%;
      margin: 0;
      column-count: 2;
      column-gap: 0;
      height: auto;

      li {
        padding: 10px;
        border-right: 0;
        line-height: 20px;
        float: none;
        // -webkit-column-break-inside: avoid; /* Chrome, Safari, Opera */
        page-break-inside: avoid; /* Firefox */
        // break-inside: avoid; /* IE 10+ */

        a {
          min-width: 150px;
          display: inline-block;
        }
      }
    }
  }

  #copyright {
    float: left;
    padding: 10px;
    background: none;
    background-image: none !important;

    img, img.US {
      float: left;
      margin: 0 10px 0 0;
    }
  }
}



