@import "../../../../../../_sass/sass-includes";

/* NOTE: The below utilizes progressive enhancement. Mobile-first, then desktop. */
/*===== General/High-level =====*/
.toolbar {
    font-size: 11px;
    position: relative;
    background-color: white;

    button, input[type=button] {
        background-color: #333;
        background-image: -webkit-linear-gradient(top, #333 0%, #191919 100%);
        background-image: linear-gradient(to bottom, #333 0%, #191919 100%);
        border: 1px solid #000;
        -webkit-border-radius: 4px;
        -ms-border-radius: 4px;
        -moz-border-radius: 4px;
        -o-border-radius: 4px;
        border-radius: 4px;
        color: #fff;
        font-size: 10px;
        -webkit-font-smoothing: antialiased;
        font-weight: bold;
        height: 22px;
        line-height: 20px;
        padding: 0;

        &:focus {
            outline: none;
        }

        &:hover {
            cursor: pointer;
        }

        &:active {
            background-color: #191919;
            background-image: -webkit-linear-gradient(top, #191919 0%, #333 100%);
            background-image: linear-gradient(to bottom, #191919 0%, #333 100%);
        }

        &[disabled=true],
        &[disabled] {
            background: none #dadada;
            border: 1px solid #dadada;
            color: #fff;
            cursor: not-allowed;
        }

        &.go {
            padding: 0 5px;
        }

        &.utility-btn {
            background-color: $blue18;
            background-image: none;
            border: 1px solid $_BE;
            color: $_F;
            font-size: 11px;
            padding: 0 5px;

            &::-moz-focus-inner {
                border: 0;
                padding: 0;
            }

            &.button-active,
            &.button-active:active {
                background-color: $_F;
                background-image: -webkit-linear-gradient(top, $_F 0%, $_F1 100%);
                background-image: linear-gradient(to bottom, $_F 0%, $_F1 100%);
                color: $blue18;
            }
        }

        &.utility-btn .icon {
            cursor: pointer;
            display: inline-block;

            &--print {
                &:before {
                    background-image: printIcon($_F);
                }
            }

            &--legend {
                &:before {
                    background-image: legendIcon($_F);
                }
            }

            &--filter {
                &:before {
                    background-image: filterIcon($_F);
                }
            }

            &:before {
                background-repeat: no-repeat;
                -webkit-background-size: 13px 13px;
                -moz-background-size: 13px 13px;
                -o-background-size: 13px 13px;
                background-size: 13px 13px;
                content: '';
                float: left;
                height: 13px;
                margin: 4px 5px 4px 0;
                min-height: 13px;
                width: 13px;
            }

            &.button-active:before {
                background-image: none;
                content: '×';
                font-family: 'Sintony-Bold', sans-serif;
                font-size: 24px;
                margin: 0 5px 0 0;
            }
        }
    }
}

/*===== Inputs =====*/
.toolbar select {
    // max-width: 105px;
    max-width: none;
    width: 100%;
}

.toolbar .select-component-container + .select-component-container {
    margin-top: 10px;
}

// Bump the GO button up to match the selects
.toolbar__row button {
    align-self: flex-end;
    margin-bottom: 5px;
}

.toolbar__row .filter--aggregate {
    margin-bottom: 5px;
    width: 100%;
}

#aggregateCheckbox {
    margin: 0 5px 0 0;
    vertical-align: middle;
}

.range-container {
    padding: 0 8px;

    .rc-slider {
        &-disabled {
            background-color: transparent;
        }
    }
}

.legend-button,
.print-button,
.stats-legend-container {
    display: none;
}

/*===== Rows ====*/
// Rows should take up the full width and employ flexbox to enable responsive behavior
.toolbar__row {
    display: flex;
    flex-flow: row wrap;
    width: 100%;
}

.toolbar__row + .toolbar__row {
    border-top: 1px solid $_D2;
    padding-top: 5px;
}

.toolbar__row.toolbar__row--filters {
    border-bottom: none;
    margin-bottom: 0;

    .toolbar__col:last-child {
        border-bottom: none;
    }
}

/* Filter sections (the divisions within a row) also should have flexbox so their contents can move fluidly. The width
 * 100% is so they take up the full width on mobile. They also have borders to match their desktop layout. */
.toolbar__col {
    // align-items: flex-start;
    border-bottom: 1px solid $_D2;
    // display: flex;
    // flex-flow: row wrap;
    padding: 15px;
    width: 100%;
}

// filter__realtime is the last child, but its styles are specific to its components hence why it's not with :last-child
.toolbar__col.filter__realtime {
    padding-top: 20px;
}

// Bottom row  -- Result filters, Print & Legend buttons
.toolbar__row .filters__result-filters {
    width: 220px;

    #searchInput {
        max-width: 180px;
    }
}

// Legend & Print buttons are on the last row, but need to be pushed to the right hence the margin-left:auto
.toolbar__row .legend-button {
    margin-left: auto;
}

.toolbar__row .print-button {
    margin-left: 10px;
}

/*===== Filter components/groupings =====*/
.filter-section label {
    display: block;
    margin-bottom: 5px;
}

.filter-section input + label {
    display: inline-block;
}

.filter {
    padding-top: 5px;
}

.filter > label,
.filter-section__subgroup > label {
    display: block;
    font-size: 10px;
    margin: 0 0 10px;
    text-transform: uppercase;
}

/*===== Advanced Filters =====*/
.toolbar__col.advanced-filters {
    flex-flow: column wrap;
}

.toolbar__col.advanced-filters label {
    display: block;
    margin: 5px 0 10px;
}

.toolbar__col.advanced-filters input + label {
    display: inline-block;
    margin-left: 5px;
}

/*===== Media queries =====*/
@media only screen and (min-width: 1024px) {

    /*===== General =====*/

    .toolbar select {
        max-width: 105px;
    }

    .toolbar .select-component-container + .select-component-container {
        margin-top: 0;
    }

    /*===== Rows =====*/
    .toolbar__row {
        padding: 0 20px;
    }

    /*===== Filter subsections =====*/
    .toolbar__col {
        border-bottom: none;
        padding: 5px 0;
        position: relative;
        width: auto;
    }

    // Most filter sections don't take up the full width, so make the search bar/print/legend row full width
    .toolbar__col.filter__realtime {
        padding-top: 0;
        width: 100%;
    }

    // Re-align any buttons
    .toolbar__row button {
        align-self: flex-start;
        margin-left: 5px;
        margin-top: 26px;
    }

    .legend-button,
    .print-button,
    .stats-legend-container {
        display: block;
    }

    .utility-buttons {
        flex-grow: 2;

        .utility-btn {
            margin: 26px 10px 0 0;
        }

        .legend-button {
            margin-left: auto;
        }
    }
}

//ad specific stuff
.navi-sap-header-ad {

    &.desktop {
        display: flex;
        margin-left: auto;

        @include respond-to(mobile) {
            display: none;
        }
    }

    &.mobile {
        display: none;

        @include respond-to(mobile) {
            display: flex;

            & > div {
                margin: 0 auto;
                display: flex;
            }
        }
    }
}

@import 'print';



