/*==== Sliders ====*/
.rc-slider {
  padding: 10px 0;
  margin-bottom: 10px;

  .rc-slider-track {
    background-color: #0183da;
  }

  .rc-slider-handle {
    border: 4px solid #0183da;
    height: 20px;
    margin-top: -8px;
    width: 20px;
  }
}

/*===== Media queries =====*/
@media only screen and (min-width: 1024px) {
  .rc-slider {
    padding: 5px 0;

    .rc-slider-handle {
      border: 2px solid #0183da;
      height: 14px;
      margin-top: -5px;
      width: 14px;
    }
  }
}



