.index .star-container {
	align-items: flex-start;
	justify-content: space-around;
  display: flex;
  flex-flow: row wrap;
  margin-top: 20px;
  width: 100%;

	ul {
		// display: flex;
		// flex-direction: column;
    padding: 20px 0;

		li {
			// display: flex;
			// flex-direction: row;
			// align-items: center;
      padding: 10px 0;

      .star-attribute {

        h3 {
          display: inline-block;
          font-family: Oswald-Regular, sans-serif;
          font-size: 26px;
          line-height: 20px;
          min-width: 25px;
          text-align: center;
          vertical-align: middle;
          width: 40%;
        }

        span {
          display: inline-block;
          font-family: Arial,sans-serif;
          font-size: 10px;
          font-weight: 700;
          min-width: 25px;
          padding-right: 10px;
          text-align: right;
          text-transform: uppercase;
          vertical-align: middle;
          width: 60%;
        }
      }
		}
	}

  .stats-leader {
    margin: 0;
  }
}


@media only screen and (min-width: 1024px) {
  .index .star-container {
    width: 33%;
  }
}



