.leaders__group {
  background-color: #fff;
  box-shadow: 0 2px 3px rgba(0,0,0,.1);
  margin-bottom: 20px;
  padding: 2%;
  width: 100%;
}

.leaders__group h1 {
  font-family: Sintony-Bold, sans-serif;
  font-size: 29px;
  font-weight: bold;
  line-height: 29px;
  text-transform: uppercase;
}

.leaders__group .loading-overlay {
  display: block;
  text-align: center;
  width: 100%;
}

.leaders--no-data {
  border-top: 1px solid #d6d6d6;
  font-size: 14px;
  margin-top: 20px;
  padding-top: 20px;
  text-align: center;
  width: 100%;

  span {
    font-weight: bold;
  }
}

/*===== Media queries =====*/
@media only screen and (min-width: 1024px) {
  .leaders__group {
    display: flex;
    flex-flow: row wrap;
  }

  .leaders__group header {
    width: 100%;
  }
}




