.filter__range input,
.filter__range select {
  width: 84px;
}

// Specific to range inputes (i.e. date or season range)
.time-frame-to-label {
  padding: 0 5px;
}

.filter-range-container {
  margin-bottom: 10px;
}

.filter-range-container label {
  font-family: verdana, sans-serif;
  font-size: 10px;
  text-transform: uppercase;
}

.filter-range-container input {
  max-width: 84px;
}

.filter-range-container input[type="radio"] {
  margin: 0 5px 0 0;
  vertical-align: middle;
}

.filter-range-container .filter__range {
  margin: 5px 0 0 18px;
}

.filter__range > div {
  display: inline-block;
}

// Special case for the calendar inputs since they are text instead of selects
.filter__range--calendar {
  padding: 2px 0;

  .input-calendar:last-child {
    padding-right: 0;
  }
}

/*===== Media queries =====*/
@media only screen and (min-width: 1024px) {

  // Set up the Time Frame section so the two range selections are next to each other with the checkbox underneath
  .filter-section--timeframe {
    display: block;
  }

  .filter-range-container {
    align-items: center;
    display: flex;
  }

  .filter-range-container label {
    margin-right: 10px;
  }

  .filter-range-container .filter__range {
    margin: 0 0 0 auto;
  }

}



