@import '../../common/footer-styles';

.glossary {
  background: #fff;
  padding: 32px 20px;

  header {
    margin-bottom: 20px;
  }

  figure:not(:last-child) {
    margin-bottom: 20px;
  }

  figure {
    font-size: 16px;
    margin-bottom: 20px;

    figcaption {
      font-weight: bold;
      font-size: 18px;
    }
  }
}



