@import "../../../../../../_sass/sass-includes";

// PlayerStatComponent
.silo-player {
	display: flex;
	flex-direction: column;
	align-items: center;

	& .logo-headshot {

		@include respond-to(mobile) {
			width: 80px;
		}

		& .team-logo {
			@include size(72px 48px);
			position: absolute;
			margin-left: 75px;
			margin-top: 75px;

			@include respond-to(mobile) {
				@include size(48px 32px);
			}
		}

		& .headshot {
			& .player-avatar {
				&__img {
					border-radius: 50%;
					@include size(120px);
					border: 1px solid $_D2;

					@include respond-to(mobile) {
						@include size(80px);
					}
				}
			}
		}
	}

	& .player-details {
		display: flex;
		flex-direction: column;
		align-items: center;

		p {
			@include fontpair(16px 20px);
			font-family: $sintony;
			font-weight: 700;
			color: $_25;

			@include respond-to(mobile) {
				@include fontpair(12px 16px);
			}

			&.player-name-number-container {

				@include respond-to(mobile){
					font-size: 14px;
				}

				& .number-container {
					display: inline-block;
					vertical-align: top;
					padding-right: 5px;
					line-height: 30px;
					font-size: 20px;

					& .hash {
						font-size: 12px;
						display: inline-block;
						vertical-align: top;
					}
				}

				& .name-container {
					display: inline-block;
					border-left: 1px solid $_6;
					padding-left: 5px;
					text-align: left;
					line-height: 15px;

					span {
						display: block;
						text-align: left;
					}
				}

				a {
					color: $_25;

					&:hover {
						color: $blue18;
					}
				}
			}

			&.team-name-position-container {

				font-family: $sintony;
				@include fontpair(13px 22px);
				color: $_6;

				@include respond-to(mobile){
					display: none;
				}

				& span {
					padding: 0 5px;
					border-right: 1px solid $_6;
				}

				& span.position {
					border: 0;
				}
			}
		}
	}
}

// TeamStatComponent
.team-stats-component {

	img {
		display: block;
		width: 120px;
		height: 80px;
		margin: 0 auto;
	}
}


