.leaders-container {
  margin-top: 30px;
}

.leaders-container h1 {
  font-size: 14px;

  &:after {
    content: ' \203A';
    margin-left: 5px;
  }
}

.leaders-container .leader-requirements {
  font-size: 12px;
}

.leaders-container .league-average {
  text-align: center;
}


@media only screen and (min-width: 1024px) {
  .leaders-container {
    display: flex;
    flex-flow: row wrap;
    width: 50%;

    &:nth-child(even) {
      border-right: 1px solid #d6d6d6;
    }

    &:nth-child(odd) {
      padding-left: 30px;
    }
  }

  .leaders-container header {
    margin-bottom: 25px;
    width: 100%;
  }

  .leaders-container header h1 {
    font-family: 'Sintony-Regular', sans-serif;
    font-size: 20px;
    font-weight: 400;
    line-height: 20px;
  }

  .leaders-container .stats-leader,
  .leaders-container .stats-leaders-list {
    width: 50%;
  }

  .leaders-container .stats-leaders-list {
    padding-right: 10px;
  }

  .leaders-container .league-average {
    margin-left: auto;
    width: 50%;
  }
}



