@import "../../../../../../_sass/sass-includes";

#statsHeaderContainer {
	width: 100%;
	float:left;
	overflow: hidden;
	border-bottom: 1px solid $_D6;
	text-transform: uppercase;
	position: relative;
	z-index: 10;
	background-color: $_F;
	padding: 0;

	& h1 {
		margin: 0;
		color: $_25;
		font-size: 42px;
		line-height: 52px;
		text-transform: none;
		padding: 32px 0 32px 20px;
		// Should be Sintony, font-weight 500

		@include respond-to(mobile){
			font-size: 30px;
			line-height: 40px;
			text-transform: none;
			padding: 20px 0 20px 10px;
		}

	}

	& ul { //#statsNavi
		border-top: 1px solid $_D2;
		margin: 0;
		display: flex;
		justify-content: flex-start;

		@include respond-to(mobile){
			justify-content: space-around;
		}

		& li {
			display: inline-block;
			position: relative;

			& a {
				padding: 15px 0;
				color: $_45;
				border-radius: 0;
				position: relative;
				display: block;
				margin: 0 12px;

				@include respond-to(mobile){
					margin: 0;
				}

				&:link, &:visited {
					font-family: Sintony, Arial, sans-serif;
					font-size: 18px;

					@include respond-to(mobile){
						font-size: 12px;
						padding: 14px 0;
						margin: 0;
					}
				}

				&.active:link, &.active:visited {
					color: $_25;
					font-weight: 700;
					background-color: $_F;
					border-bottom: 4px solid $blue18;
					cursor: default;
					padding-bottom: 20px;
					@include respond-to(mobile) {
						padding: 14px 0;
					}
				}

				&:hover {
					border-bottom: 4px solid $blue18;
					padding-bottom: 20px;

					@include respond-to(mobile){
						padding-bottom: 14px;
					}
				}
			}
		}
	}
}



