.milestone-chart {
	//clear: both;
	height: 100px;
	margin-right: 5%;
	margin-left: 5%;
}

.milestone .stat-details {
  text-align: center;

  a {
    color: #333;

    &:active,
    &:visited {
      color: #333;
    }

    p {
      font-weight: 700;
      margin: 5px 0;;
    }

    p + p {
      color: #868686;
      font-size: 12px;
      font-weight: 400;
      line-height: 12px;
      margin: 8px 0 0 0;
    }
  }
}



