@import "../../../../_sass/sass-includes";
@import "../../common/footer-styles";

#pageBody, #pageBody * {
	-webkit-box-sizing: border-box;
	-moz-box-sizing: border-box;
	box-sizing: border-box;
}

#pageBody {
	padding: 0;
}

a {
	&:hover {
		color: $blue18;
	}

	&.arrow-link:after {
		margin-left: 5px;
		content: ' \203A';
	}
}

h1 {
  font-family: $sintony-bold;
  font-weight: 400;
  @include fontpair(24px);
}

h2 {
  font-family: $sintony;
  font-weight: 400;
  font-size: 30px;
}

h3 {
  font-family: $sintony;
  font-weight: 400;
  @include fontpair(20px);
}

h4 {
  font-family: Arial, sans-serif;
  @include fontpair(12px);
  font-weight: bold;
  text-transform: uppercase;
}

header {
  display: flex;
  font-family: Sintony-Bold, sans-serif;
  @include fontpair(29px);
  text-transform: uppercase;
}

input:disabled + label,
input[disabled=true] + label,
input[disabled] + label {
    color: #b6b6b6;
}

select {
  background-color: #fff;
  background-image: -webkit-linear-gradient(top, #fff 0%, #f1f1f1 100%);
  background-image:         linear-gradient(to bottom, #fff 0%, #f1f1f1 100%);
  border: 1px solid #bebebe;
  color: #272727;
  font-size: 11px;
  height: 22px;
  line-height: 22px;
  max-width: 105px;

  &:focus {
    outline: none;
  }

  &[disabled=true],
  &[disabled] {
    color: #d6d6d6;
  }
}


/*===== Media queries =====*/
@media only screen and (min-width: 1024px) {
  #pageBody {
    padding: 0 10px;
  }
}



