/*===== Media queries =====*/
@media only screen and (min-width: 1024px) {
  .toolbar__row--report-options .toolbar__col {
    align-items: flex-start;
    display: flex;

    // Padding short cut not used to not overwrite existing padding-top/bottom
    padding-left: 20px;
    padding-right: 20px;

    // Flexbox can't provide the specific spacing we need so here we're placing padding on all columns and removing the
    // left/right hand padding, respectively, on the first and last rows.
    &:first-child {
      padding-left: 0;
    }

    &:last-child {
      padding-right: 0;
    }
  }

  // Target the items in the second filter division and give each filter some space
  // If we were to use the same approach as the filter
  .toolbar__row--report-options .toolbar__col.non-time-report-options {
    div + div {
      margin: 0 10px;
    }
  }

  // Create the divider between sections
  .toolbar__row--report-options .toolbar__col:not(:last-child):after {
    background: #d2d2d2;
    content: '';
    height: calc(100% - 20px);
    position: absolute;
    right: 0;
    top: 50%;
    transform: translateY(-50%);
    width: 1px;
  }

}



